.menu-btn{position:absolute;z-index:3;right:35px;top:35px;cursor:pointer;transition:all 0.5s ease-out}.menu-btn .btn-line{width:28px;height:3px;margin:0 0 5px 0;background:#fff;transition:all 0.5s ease-out}.menu-btn.close{transform:rotate(180deg)}.menu-btn.close .btn-line:nth-child(1){background:red;transform:rotate(45deg) translate(5px, 5px)}.menu-btn.close .btn-line:nth-child(2){opacity:0}.menu-btn.close .btn-line:nth-child(3){background:red;transform:rotate(-45deg) translate(7px, -6px)}.menu{position:fixed;top:0;width:100%;opacity:0.9;visibility:hidden}.menu.show{visibility:visible}.menu-branding,.menu-nav{display:flex;flex-flow:column wrap;align-items:center;justify-content:center;float:left;width:50%;height:100vh;overflow:hidden}.menu-nav{margin:0;padding:0;background:#373737;transform:translate3d(0, -100%, 0);transition:all 0.5s ease-out}.menu-nav.show{transform:translate3d(0, 0, 0)}.menu-branding{background:#444;transform:translate3d(0, 100%, 0);transition:all 0.5s ease-out}.menu-branding.show{transform:translate3d(0, 0, 0)}.menu-branding .portrait{width:250px;height:250px;background:url("../images/portrait.jpg");border-radius:50%;border:3px solid #eece1a}.menu .nav-item{transform:translate3d(600px, 0, 0);transition:all 0.5s ease-out}.menu .nav-item.show{transform:translate3d(0, 0, 0)}.menu .nav-item.current>a{color:#eece1a}.menu .nav-link{display:inline-block;position:relative;font-size:30px;text-transform:uppercase;padding:1rem 0;font-weight:300;color:#fff;text-decoration:none;transition:all 0.5s ease-out}.menu .nav-link:hover{color:#eece1a}.nav-item:nth-child(1){transition-delay:.1s}.nav-item:nth-child(2){transition-delay:.2s}.nav-item:nth-child(3){transition-delay:.3s}.nav-item:nth-child(4){transition-delay:.4s}*{box-sizing:border-box}body{background:#444;color:#fff;margin:0;font-family:'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;line-height:1.5}body#bg-img{background:url(../images/background.jpg);background-attachment:fixed;background-size:cover}body#bg-img:after{content:'';position:absolute;top:0;right:0;width:100%;height:100%;background:rgba(68,68,68,0.9);z-index:-1}h1,h2,h3{margin:0;font-weight:400}h1.lg-heading,h2.lg-heading,h3.lg-heading{font-size:6rem}h1.sm-heading,h2.sm-heading,h3.sm-heading{margin-bottom:rem;padding:0.2rem 1rem;background:rgba(73,73,73,0.5)}a{color:#ffffff;text-decoration:none}header{position:fixed;z-index:2;width:100%}.text-secondary{color:#eece1a}main{padding:4rem;min-height:calc(100vh - 60px)}main .icons{margin-top:1rem}main .icons a{padding:.4rem}main .icons a:hover{color:#eece1a;transition:all 0.5s ease-out}main#home{overflow:hidden}main#home h1{margin-top:20vh}.about-info{display:grid;grid-gap:30px;grid-template-areas:'bioimage bio bio' 'job1 job2 job3';grid-template-columns:repeat(3, 1fr)}.bio-image{grid-area:bioimage;margin:auto;border-radius:50%;border:3px solid #eece1a}.bio{grid-area:bio;font-size:1.5rem}.job-1{grid-area:job1}.job-2{grid-area:job2}.job-3{grid-area:job3}.job{background:#515151;padding:0.5rem;border-bottom:#eece1a 5px solid}#main-footer{text-align:center;padding:1rem;background:#2b2b2b;color:#fff;height:60px}@media screen and (max-width: 768px){main{align-items:center;text-align:center}main .lg-heading{line-height:1;margin-bottom:1rem}ul.menu-nav,div.menu-branding{float:none;width:100%;min-height:0}ul.menu-nav.show,div.menu-branding.show{transform:translate3d(0, 0, 0)}.menu-nav{height:75vh;transform:translate3d(-100%, 0, 0);font-size:24px}.menu-branding{height:25vh;transform:translate3d(100%, 0, 0)}.portrait{background:url("../images/portrait_small.jpg");width:150px;height:150px}.about-info{grid-template-areas:'bioimage' 'bio' 'job1' 'job2' 'job3';grid-template-columns:1fr}}@media screen and (max-width: 500px){main#home h1{margin-top:10vh}}
